import React, { Component } from 'react'
import { nanoid } from 'nanoid'
import { formatDistance } from 'date-fns'
import ETHTornado from '../../ethereum/ETHTornado'
import './Home.css'

class Stats extends Component {
  constructor(props) {
    super(props)
    this.state = {
      usrIP: props.usrIP,
      uCount: props.uCount,
      arrTx: [],
    }
  }
  
  async componentDidMount () {
    let events = await ETHTornado.getPastEvents('Deposit', { fromBlock: 0, toBlock: 'latest' })
    // console.log('events', events)
    // console.log('events.length', events.length)
    if (events.length >= 18) {
      events = events.reverse().slice(0, 18)
    } else {
      events = events.reverse()
    }
    let filtered = await events.map(x => {
      let txTime = formatDistance(parseInt(x.returnValues.timestamp + '000'), Date.now())
      if (txTime.indexOf('about ') !== -1) {
        txTime = txTime.split('about ')[1]
      }
      return {
        txNo: parseInt(x.returnValues['1']) + 1,
        txTime: txTime + ' ago',
      }
    })
    // console.log('filtered', filtered)
    await this.setState({ arrTx: filtered, uCount: events.length })
  }
  
  render() {
    const { state: { usrIP, arrTx, uCount }, props: { token, tokenAmt } } = this
    return (
      <div className={'statistics'}>
        <div style={{ display: 'flex' }}>
          <div className={'statsHeader'}>Statistics</div>
          <div className={'statsTokAmt'}>{tokenAmt} {token}</div>
        </div>
        <div className={'statsSubHeader'}>Anonymity set</div>
        <div style={{ padding: '0 1vw' }}>{uCount} equal user deposits</div>
        <div className={'statsSubHeader'}>latest deposits</div>
        <div className={'recentTxns'}>
          {arrTx.map((x, i) => (
            <div
              key={nanoid(4)}
              style={{ padding: '0 1vw', backgroundColor: `${i % 2 === 1 ? '#1a1a1a' : '#212121'}` }}>
              {x.txNo}. {x.txTime}
            </div>
          ))}
        </div>
        <div className={'statsIP'}>
          <div>Your IP</div>
          <div>{usrIP}</div>
        </div>
      </div>
    )
  }
}

export default Stats
