import React, { Component } from 'react'
// import MakeWithdraw from '../../../ethereum/MakeWithdraw'
// import MakeWithdraw2 from '../../../ethereum/MakeWithdraw2'
import '../Home.css'

class Withdraw extends Component {
  constructor(props) {
    super(props)
    this.state = {
      note: 'cardnado-eth-0.1-4-0x75294fde5c1803dd2980c830a1cbdae8e9cb66cc126e19bdd801a57bef0b542a705401324c9ffc946711818d1fa9d1eeb947e0eac3335d50cca3f562cb51',
      recipient: '0x98AbD72956B4E362A03a947B55C89cc6e00A78E6',
      errMsg: '',
      pending: false,
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleDonate = this.handleDonate.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleReq = this.handleReq.bind(this)
  }
  
  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  
  handleDonate() {
    const { REACT_APP_ETH_TORNADO_ADDRESS } = process.env
    this.setState({ recipient: REACT_APP_ETH_TORNADO_ADDRESS })
  }
  
  handleSubmit(e) {
    e.preventDefault()
    const { note, recipient } = this.state
    if (note.length > 0 && recipient.length > 0) {
      this.handleReq({ note: note, recipient: recipient })
    }
  }
  
  async handleReq({...args}) {
    // await MakeWithdraw(args.note, args.recipient)
    await this.setState({ pending: true })
    try {
      await window._MakeWithdrawal(args.note, args.recipient)
    } catch(e) {
      console.log('err', e)
      this.setState({ errMsg: e.message })
    }
    await this.setState({ pending: false })
  }
  
  render() {
    const { recipient, note } = this.state
    return (
      <form onSubmit={this.handleSubmit} style={{ display: 'flex', flexDirection: 'column', width: '25vw' }}>
        <div style={{ padding: '0 1vw' }}>Note</div>
        <input
          type={'text'}
          name={'note'}
          value={note}
          onChange={this.handleChange}
          className={'withdrawInput'}/>
        <div className={'withdrawRecipHeader'}>
          <div>Recipient Address</div>
          <div style={{ color: '#1F33E8', cursor: 'pointer' }} onClick={this.handleDonate}>Donate</div>
        </div>
        <input
          type={'text'}
          name={'recipient'}
          value={recipient}
          onChange={this.handleChange}
          className={'withdrawInput'}/>
        <input
          type={'submit'}
          value={'Withdraw'}
          className={'homeConnect'}
          style={{
            // width: '80px', // for mobile?
            width: '10vw',
            margin: '5vh 0',
            alignSelf: 'center',
            color: '#D7DADC',
            border: '1px solid #1F33E8',
            padding: '1vw 1vh',
            textAlign: 'center',
            borderRadius: '2px',
            backgroundColor: 'transparent',
            cursor: 'pointer',
          }}/>
        {this.state.pending ?
          <div className={'loadC'} style={{ margin: '0 auto' }}>
            <div id={'lodr'}></div>
            <div id={'inC'}></div>
          </div>
          :
          null
        }
        {this.state.errMsg !== '' ?
          <div style={{ margin: '0 auto' }}>{this.state.errMsg}</div>
          :
          null
        }
      </form>
    )
  }
}

export default Withdraw
