import React, { Component } from 'react'
import { DropdownButton, Dropdown } from 'react-bootstrap'
import Connect from '../../Reuse/Connect'
import RadioBtn from '../../Reuse/RadioBtn'
import web3 from '../../../web3'
import MakeDeposit from '../../../ethereum/MakeDeposit'
import Copy from '../../../Assets/Copy.svg'
import Save from '../../../Assets/Save.svg'
import '../Home.css'

class Deposit extends Component {
  constructor(props) {
    super(props)
    this.state = {
      denomination: '0.1',
      token: 'ADA',
      connected: false,
      note: '',
      pending: false,
    }
    this.handleTok = this.handleTok.bind(this)
    this.handleAmt = this.handleAmt.bind(this)
    this.handleDeposit = this.handleDeposit.bind(this)
    this.handleCopy = this.handleCopy.bind(this)
    this.saveFile = this.saveFile.bind(this)
  }
  
  async componentDidMount () {
    const accounts = await web3.eth.getAccounts()
    if (accounts.length > 0) {
      this.setState({ connected: true })
    }
  }
  
  async handleTok(e) {
    let { innerText } = e.target
    this.props.tokSelected(innerText)
    this.setState({ token: innerText })
  }
  
  async handleAmt(denom) {
    await this.setState({ denomination: denom })
    this.props.amtSelected(denom)
  }
  
  async handleDeposit() {
    console.log(`%chandleDeposit()`,'color:lawngreen')
    const { denomination } = this.state
    await this.setState({ pending: true })
    let NOTE
    try {
      NOTE = await MakeDeposit(denomination)
    } catch (e) {
      this.setState({ errMsg: e.message })
    }
    await this.setState({ note: NOTE, pending: false })
  }
  
  handleCopy() {
    navigator.clipboard.writeText(this.state.note)
  }
  
  async saveFile() {
    function downloadFi(text) {
      let a = document.createElement('a')
      let file = new Blob([text], { type: 'text/plain' })
      a.href = URL.createObjectURL(file)
      a.download = `backup-${text.slice(0, 29)}.txt`
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
    }
    await downloadFi(this.state.note)
  }
  
  render() {
    const { token, tokenAmt  } = this.props
    const { Divider, Item } = Dropdown
    
    const DropDownCoin = () => (
      <div className={'ddTitle'}>
        <div style={{ margin: '0 .5vw 0 0' }}>{token}</div>
        <div className={'ddArrow'}>
        </div>
      </div>
    )
    
    return (
      <div style={{
        display: 'flex',
        flexDirection: 'column',
      }}>
        <div style={{ padding: '0 4px' }}>Token</div>
        <DropdownButton id={'dd-btn'} title={
          <DropDownCoin/>
        }>
          <div className={'tokDropDown'}>
            <Item onClick={this.handleTok} className={'tokName'}>ADA</Item>
            <Divider/>
            <Item onClick={this.handleTok} className={'tokName'}>DAI</Item>
            <Divider/>
            <Item onClick={this.handleTok} className={'tokName'}>cDAI</Item>
            <Divider/>
            <Item onClick={this.handleTok} className={'tokName'}>USDC</Item>
            <Divider/>
            <Item onClick={this.handleTok} className={'tokName'}>USDT</Item>
            <Divider/>
            <Item onClick={this.handleTok} className={'tokName'}>WBTC</Item>
          </div>
        </DropdownButton>
        
        <div style={{ padding: '0 4px' }}>Amount</div>
        
        <div style={{ display: 'flex', justifyContent: 'space-evenly' }}>
          <RadioBtn amt={'0.1'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
          <RadioBtn amt={'1'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
          <RadioBtn amt={'10'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
          <RadioBtn amt={'100'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
        </div>
        
        {this.state.connected
          ? <Connect onClick={this.handleDeposit} txt={'Deposit'}/>
          : <Connect to={'/account'} className={'homeConnect'}/>
        }
        
        {this.state.note !== ''
          ? (
            <div style={{overflow: 'wrap', wordWrap: 'break-word', width: '29vw', padding: '0.5vh 0', margin: '0 0.5vw', }}>
              {this.state.note}
              <img
                alt={'copy text'}
                src={Copy}
                onClick={this.handleCopy}
                style={{ margin: '0 0.5vw', cursor: 'pointer' }}/>
              <img
                alt={'save as file'}
                src={Save}
                onClick={this.saveFile}
                style={{ margin: '0 0.5vw', cursor: 'pointer', height: '15px', width: '15px' }}/>
            </div>
          )
          : this.state.pending ?
            <div className={'loadC'} style={{ margin: '0 auto' }}>
              <div id={'lodr'}></div>
              <div id={'inC'}></div>
            </div>
            :
            null
        }
        {this.state.errMsg !== '' ?
          <div>{this.state.errMsg}</div>
          :
          null
        }
        
      </div>
    )
  }
}

export default Deposit
